body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-image: url(/static/media/background.b5d71eb0.jpg);
    background-color: #ea8c8c;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
    position: -webkit-sticky; /* Safari */
    position: sticky;
    top: 0;
    border: 3px solid rgb(0, 0, 0);
  }
  
  li {
    float: left;
  }
  
  li a {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    width: 180px;
  }
  
  li a:hover {
    background-color: #111;
  }
  
  .active {
    background-color: rgb(24, 5, 145);
  }
  
  .navbarimg {
    border-radius: 30%;
    border: 3px solid rgb(19, 4, 83);
    padding: 5px;
    width: 200px;
    position: relative;
    left: 40%;
  }
  
  .navbar {
    padding-bottom: 30px;
    float: left;
    padding-left: 5px;
  }
  
  .hidden {
    visibility: hidden;
    font-size: 1px;
  }
  
  .bannerimg {
    border-radius: 30%;
    border: 3px solid rgb(19, 4, 83);
    width: 1050px;
    height: 175px;
    position: relative;
   margin-left: 5px;
  }
  
  .card__title {
    font-size: 40px;
    font-style: italic;
    background-color: gray;
    display: block;
    text-align: center;
  }
  
  
  .null {
  
    font-size: 40em;
  
  }
  
  .card {
    display: block;
    width: 30%;
    margin: 5px;
    padding: 1em;
    background: rgba(255, 255, 255, 0.589);
    text-align: center;
    display: block;
    margin-left: auto;
    margin-right: auto;
    border: 3px solid rgb(19, 4, 83);
  }
  
  .card_overview {
    background-color: gray;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    text-align: center;
    border: 3px solid rgb(19, 4, 83);
  }
  
  .MainHeader {
    margin: 0 10px;
    font-size: 70px;
    font-style: italic;
    background-color: gray;
    text-align: center;
    border: 3px solid rgb(19, 4, 83);
    display: grid;
    grid-gap: 10px;
    margin: 20px;
  }
  
  .spacer {
    margin: 0 10px;
    padding-bottom: 20 px;
    float: left;
  }
  
  .actorimages {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 30%;
    border: 3px solid rgb(0, 0, 0);
  }
  
  .images {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 75%;
    border: 3px solid rgb(19, 4, 83);
  }
  
  .card__title {
    text-align: center;
  }
  
  input[type="text"] {
    width: 50%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: 2px solid rgb(19, 4, 83);
    border-radius: 4px;
    background-color: lightblue;
    -webkit-transition: width 0.4s ease-in-out;
    transition: width 0.4s ease-in-out;
  }
  
  .button {
    display: inline-block;
    padding: 15px 25px;
    font-size: 24px;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    outline: none;
    color: #fff;
    background-color: rgb(0, 0, 0);
    border: none;
    border-radius: 15px;
  }
  
  .button:hover {
    background-color: #3c4b3c;
  }
  
  .button:active {
    background-color: #000000;
    box-shadow: 0 5px #666;
    -webkit-transform: translateY(4px);
            transform: translateY(4px);
  }
  
  .app {
    background-color: gray;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    text-align: center;
    border: 3px solid rgb(0, 0, 0);
  }
  
  .meme {
    position: relative;
    width: 90%;
    margin: auto;
  }
  
  .meme > img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
  }
  
  .meme > h2 {
    position: absolute;
    width: 80%;
    text-align: center;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    margin: 15px 0;
    padding: 0 5px;
    font-family: impact, sans-serif;
    font-size: 3em;
    text-transform: uppercase;
    color: white;
    letter-spacing: 1px;
    text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000,
      -2px 2px 0 #000, 0 2px 0 #000, 2px 0 0 #000, 0 -2px 0 #000, -2px 0 0 #000,
      2px 2px 5px #000;
  }
  
  .meme > .bottom {
    bottom: 0;
  }
  
  .meme > .top {
    top: 0;
  }
  
  .meme-form {
    width: 90%;
    margin: 20px auto;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  
  .meme-form > input {
    width: 45%;
    height: 40px;
  }
  
  .meme-form > button {
    border: none;
    font-family: VT323, monospace;
    font-size: 25px;
    letter-spacing: 1.5px;
    color: white;
    background: #6441a5;
  }
  
  .meme-form > input::-webkit-input-placeholder {
    /* Chrome/Opera/Safari */
    font-family: VT323, monospace;
    font-size: 25px;
    text-align: cen;
  }
  .meme-form > input::-moz-placeholder {
    /* Firefox 19+ */
    font-family: VT323, monospace;
    font-size: 25px;
    text-align: cen;
  }
  .meme-form > input:-ms-input-placeholder {
    /* IE 10+ */
    font-family: VT323, monospace;
    font-size: 25px;
    text-align: cen;
  }
  .meme-form > input:-moz-placeholder {
    /* Firefox 18- */
    font-family: VT323, monospace;
    font-size: 25px;
    text-align: cen;
  }
  
  .footer {
    font-size: 1em;
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: black;
    color: white;
    text-align: center;
  }


